import { Component } from '@angular/core';
import { Tiers } from '../../models/tiers';
import { Motif } from '../../models/motif';
import { EncaisseServiceProvider } from '../../providers/encaiss-service/encaiss-service';
import {  ViewController, NavController, NavParams } from 'ionic-angular';
import { HelperServiceProvider } from '../../providers/helper-service/helper-service';
import { SelectSearchableComponent } from 'ionic-select-searchable';

/**
 * Generated class for the MenuFilterComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'menu-filter',
  templateUrl: 'menu-filter.html'
})
export class MenuFilterComponent {
  public localDateDebut: Date = new Date();
  public localDateFin: Date = new Date();
  dateDebut: string = null;
  dateFin: string = null;
  tiers: Tiers = new Tiers();
  motif: Motif = new Motif();
  tiersAll: Tiers = new Tiers();
  listTiers: Tiers[];
  listMotifs: Motif[];
  filterActivated = false;
  compteList = [];
  codeCompte: string = "all";
  constructor(private encaisseService: EncaisseServiceProvider,
    public helperService: HelperServiceProvider,
    public viewCtrl: ViewController,
    public navCtrl: NavController,
    public navParams: NavParams
  ) {
  }
  ionViewDidEnter() {
  }
  ngOnInit() {
    this.getMotifs();
    this.getTiers();
    this.getCaisses();
    this.localDateDebut = this.navParams.get('dateDebut');
    this.localDateFin = this.navParams.get('dateFin');
    this.motif = this.navParams.get('motif');
    this.tiers = this.navParams.get('tiers');
    this.codeCompte = this.navParams.get('codeCompte');
  }
  getTiers() {
    this.listTiers = this.encaisseService.tiersListForFilter;
    this.tiers = this.encaisseService.tiersFilter;
  }
  getMotifs() {
    this.listMotifs = this.encaisseService.motifListForFilter;
    this.motif = this.encaisseService.motifFilter;
    console.log(this.listMotifs);
  }
  getCaisses() {
    this.compteList = this.encaisseService.caissesList;
  }
  setDateDebut(date: Date) {
    this.localDateDebut = date;
    this.dateDebut = this.localDateDebut.toISOString();
  }
  setDateFin(date: Date) {
    this.localDateFin = date;
    this.dateFin = this.localDateFin.toISOString();
  }
  public close() {
    this.navCtrl.pop();
  }
  public desactiver() {
    this.viewCtrl.dismiss({
      filter: false,
    });
  }
  public filter() {
    this.viewCtrl.dismiss({
      filter: true,
      dateDebut: this.localDateDebut,
      dateFin: this.localDateFin,
      tiers: this.tiers,
      motif: this.motif,
      compte: this.codeCompte
    });
  }
  tiersChange(event: { component: SelectSearchableComponent, value: any }) {
  }

}



