import { elementAt } from "rxjs/operator/elementAt";
import { DecimalPipe } from "@angular/common";

export class DataChart {
    values: number[] ;
    labels: string[];


    constructor(tab: any[], champLabel: string, champValue: string) {
        this.values = [];
        this.labels= [];
        let value: number;
        let label: string;
        tab.forEach(element => {

            if (Reflect.get(element, champLabel)) {
                label = Reflect.get(element, champLabel)
                this.labels.push(label);
            }
            if (Reflect.get(element, champValue)) {
                value = Reflect.get(element, champValue);
                this.values.push(Reflect.get(element, champValue));
            }
        });
    }
}


